@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css?family=Roboto+Mono');
@import url('https://fonts.googleapis.com/css?family=Bai+Jamjuree');

@font-face {
  font-family: 'StarJedi';
  src: url('./fonts/Starjedi.ttf') format('truetype');
}
@font-face {
  font-family: 'StarJediHollow';
  src: url('./fonts/Starjhol.ttf') format('truetype');
}
@font-face {
  font-family: 'StarJediOutline';
  src: url('./fonts/Starjout.ttf') format('truetype');
}

* {
    font-family: 'Bai Jamjuree', sans-serif;
    font-size: 14px;

    --main-color: rgba(254, 219, 153, 1);
    --main-color-dark: rgb(41, 41, 41);
    --main-text-color: rgba(237, 234, 235, 1);
    --secondary-text-color: rgba(161, 155, 146, 0.9);
    --visited-color: rgba(170, 189, 181, 0.2);
    --main-color-vivid: rgb(245, 205, 58);
    --complementary-color: rgb(79, 186, 174);
    --background-color: rgb(12, 100, 164);
    --background-color-dark: rgb(64, 66, 87);;

    background-color: var(--background-color-dark);
    color: var(--main-text-color);

    padding: 0;
    margin: 0;
}

.App {
    width: 90%;
    height:100%;
    margin: auto;
    background-color: white;
}

#root {
    background-image: -webkit-gradient(linear, left top, left bottom, from(var(--background-color)), to(var( --background-color-dark)));
    background-image: -webkit-linear-gradient(top, var(--background-color), var( --background-color-dark));
    background-image: -o-linear-gradient(top, var(--background-color), var( --background-color-dark));
    background-image: linear-gradient(to bottom, var(--background-color), var( --background-color-dark));
    background-size: 100vh auto;
}

i {
    background-color: transparent;
    margin-right: 10px;
    color: var(--complementary-color);
}

ul {
    list-style: none;
}

li {
    padding: 3px 0;
}

.main-inner{
  display: -ms-flexbox;
  display: flex;
}


/*ENTITIES LIST and utils*/
.entities-container {
    display: inline-block;
    width: 20%;
    padding-left: 3%;
    margin-top: 10px;
}

.entities-container.full-width {
    width: 100%;
}

.entities-container ul.tabData {
    margin: 0;
    padding: 7px 0px;
    columns: 1;
    -webkit-columns: 1;
    -moz-columns: 1;
}

.entities-container.full-width ul.tabData {
    columns: 6;
    -webkit-columns:6;
    -moz-columns: 6;
}

ul.tabData {
    position: relative;
}

ul.tabData li {
    cursor: pointer;
    -webkit-transition: 0.1s ease-in-out;
    -o-transition: 0.1s ease-in-out;
    transition: 0.1s ease-in-out;
    padding-left: 10px;
}

ul.tabData li:before {
    content: ":";
    position: absolute;
    -webkit-transform: translateX(-10px);
        -ms-transform: translateX(-10px);
            transform: translateX(-10px);
    background: var(--complementary-color);
    color: var(--complementary-color);
    border-radius: 5px ;
}

ul.tabData li:hover {
    color: var(--main-color-vivid);
}

input#search {
    max-width: 40vw;
    height: 40px;
    margin-right: 6px;
    padding: 12px 20px;
    font-size: 16px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: none;
    background-color: var(--main-text-color);
    color: var(--main-color-dark);
    border-radius: 10px;
}

button#sort {
    cursor: pointer;
    height: 40px;
    margin: 6px 0;
    padding: 7px 20px;
    background: var(--main-color);
    color: var(--main-color-dark);
    border: none;
    border-radius: 10px;
    font-size: 16px;
    -webkit-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
}

button#sort:disabled{
    background:var(--visited-color);
}

button#sort:disabled:hover{
    background: var(--visited-color);
}

button#sort:hover{
    background: var(--main-color-vivid);
}

.sweet-loading {
    display: inline-block;
    margin-left: 40px;
}

.sweet-loading.centered {
    margin: 50px;
}




