/*details tab*/

.details-container {
    display: inline-block;
    width: 0%;
    border-left: 2px solid var(--complementary-color);
    padding-left: 15px;
    margin-top: 10px;
    position: relative;
}

.details-container.full-width {
    width: 100%;
}

.details{
    position: relative;
}

li.details{
    cursor: pointer;
}

.details-container li b {
    color: var(--complementary-color);
    cursor: default;
}

.details span, li a {
    margin: 0 3px;
    -webkit-transition: 0.1s ease-in-out;
    -o-transition: 0.1s ease-in-out;
    transition: 0.1s ease-in-out;
}

.details span:hover, li a:hover {
    color: var(--main-color);
}

.details span:before {
    content: "•";
    position: relative;
    right: 3px;
    color: var(--complementary-color);
}

.details span.sweet-loading:before {
    content: "";
}

li a{
    text-decoration: none;
}

.details-title {
    font-family: 'StarJedi', sans-serif;
    font-size: 20px;
    font-weight: 300;
    background-color: var(--main-color);
    color: var(--main-color-dark);
    border-radius: 5px 0px 0px 5px ;
    padding: 5px;
    padding-left: 25px;
}
